import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Pagination from '@mui/material/Pagination';
import Stack from '@mui/material/Stack';
import TablePagination from '@mui/material/TablePagination';

const dispatchedEvents = [];

/*
    Example: <srs-mui-pagination>
            </srs-mui-pagination>
*/
const SrsPagination = function (props, ref) {
  const [page, setPage] = React.useState(2);
  const [rowsPerPage, setRowsPerPage] = React.useState(10);

  const handleChangePage = (newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  return (
    <TablePagination
      container={props.ref} 
      component="div"
      count={100}
      page={page}
      onPageChange={handleChangePage}
      rowsPerPage={rowsPerPage}
      onRowsPerPageChange={handleChangeRowsPerPage}
      theme={props.muiTheme}
    />
  );
}

const styles = `
    
`
const props = [{
    name: "open",
    defaultValue: "false",
    type: "boolean"
}]

const slots = [{name: "default", allowed: ["srs-accordion-item"]}]; 

const WrappedComponent = wrapper(React.forwardRef(SrsPagination), props, "mui");
export { WrappedComponent, styles, props, dispatchedEvents, slots };